<template>
  <ParagraphWithImage
    :imgSrc="imgSrc"
    :title="title"
    :paragraph="paragraph"
    alt="User flows"
  />
</template>

<script>
import ParagraphWithImage from '../common/ParagraphWithImage';

import fitted_userflow1 from "../../assets/img/fitted_userflow1.jpg";

export default {
  components: {
    ParagraphWithImage,
  },
  data() {
    return {
      imgSrc: fitted_userflow1,
      title: "User Flows",
      paragraph: "I created a user flow for the key tasks within the app: search and filter exercise videos, exercise scheduler, add sessions to calendar, create account, tracking progress, daily challenges/rewards, and social sharing.",
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



