<template>
  <div>
    <Width100 v-bind:addPadding="true">
      <Width50>
        <Paragraph
          subtitle="The Problem"
          paragraph="Finding exercise routines for your level can be difficult, especially if you want to try something new and finding routines that fit into your schedule is not easy."
        />
        <Paragraph
          subtitle="The Solution"
          paragraph="I can encourage people who want to exercise get into physical activities by providing easy routines, guides, interactive examples, and info."
        />
        <Paragraph
          subtitle="Tools"
          paragraph="Marvelapp (Prototype), Sketch (Wireframes and Visual Design)"
        />
      </Width50>
      <Width50>
        <div class="marvel-container">
          <iframe src="https://marvelapp.com/1g2dd3hg?emb=1&iosapp=false&frameless=false" title="Prototype" width="100%" height="784" allowTransparency="true" frameborder="0"></iframe>
        </div>
      </Width50>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    Paragraph,
    Width50,
    Width100,
  },

};
</script>

<style lang="scss" scoped>
.marvel-container {
  margin: 0 auto;
  text-align: center;
}
</style>



