<template>
  <ParagraphWithImage
    :imgSrc="imgSrc"
    :title="title"
    alt="Persona"
  />
</template>

<script>
import ParagraphWithImage from '../common/ParagraphWithImage';

import fitted_persona from "../../assets/img/fitted_persona.jpg";

export default {
  components: {
    ParagraphWithImage,
  },
  data() {
    return {
      imgSrc: fitted_persona,
      title: "Persona",
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



