<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Visual Direction"
        subtitle="Setting the mood"
        paragraph="Before I begin to add my high fidelity design to my wireframe, I created a mood board to communicate the visual direction of my project."
      />
    </Width100>
    <Width100>
      <VueImage :src="moodboard1" alt="Mood Board" />
    </Width100>
    <Width100>
      <Paragraph
        v-bind:center="true"
        paragraph="I wanted to go with a colorful, more playful approach to the fitness web app. By focusing on brighter colors, bold typography, and colorful patterns, the overall app will have an inviting and less intimidating atmosphere to help users have fun while they work out."
      />
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import moodboard1 from "../../assets/img/moodboard1.jpg";

export default {
  components: {
    LineBreak,
    Paragraph,
    VueImage,
    Width100,
  },
  data() {
    return {
      moodboard1: moodboard1,
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



